"use client";

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { ChevronDown, Search } from "lucide-react";
import Link from "next/link";

export default function Navbar() {
  const menus = [
    { title: "UMROH & HAJI", href: "/umroh-haji" },
    { title: "SARIMBIT", href: "/sarimbit" },
    { title: "PERLENGKAPAN SALAT", href: "/perlengkapan-salat" },
    { title: "PALESTINE SCARF", href: "/palestine-scarf" },
    { title: "NEW PRODUCT", href: "/new-product" },
    {
      title: "PAKAIAN",
      href: "/pakaian",
      submenu: [
        { title: "Kemeja", href: "/pakaian/kemeja" },
        { title: "Tunik", href: "/pakaian/tunik" },
        { title: "Gamis", href: "/pakaian/gamis" },
      ],
    },
    { title: "SCARF", href: "/scarf" },
    { title: "ZATTA MEN", href: "/zatta-men" },
    { title: "ELZATTA KIDS", href: "/elzatta-kids" },
    { title: "INSTAN HIJAB", href: "/instan-hijab" },
  ];

  return (
    <header className="border-b border-gray-200">
      {/* Top bar */}
      <div className="flex justify-between items-center max-w-7xl mx-auto px-6 py-3">
        <div className="flex-1" />

        {/* Logo tengah */}
        <Link href="/" className="flex justify-center">
          {/* Ganti dengan gambar logo asli nanti */}
          <span className="font-semibold text-2xl text-[#4B1E32] tracking-wide">
            elzatta
          </span>
        </Link>

        {/* Icon kanan */}
        <div className="flex-1 flex justify-end items-center space-x-5 text-gray-700">
          <Search className="w-5 h-5 cursor-pointer hover:text-black" />
        </div>
      </div>

      {/* Menu utama */}
      <nav className="flex justify-center items-center space-x-7 text-xs tracking-wide uppercase font-medium text-gray-800 py-2">
        {menus.map((menu) =>
          menu.submenu ? (
            <DropdownMenu key={menu.title}>
              <DropdownMenuTrigger className="flex items-center gap-1 hover:text-black transition-colors focus:outline-none">
                {menu.title}
                <ChevronDown className="w-3 h-3 mt-[1px]" />
              </DropdownMenuTrigger>
              <DropdownMenuContent className="rounded-none border border-gray-200 px-2 py-1 text-sm">
                {menu.submenu.map((sub) => (
                  <DropdownMenuItem key={sub.title} asChild>
                    <Link
                      href={sub.href}
                      className="w-full hover:text-[#4B1E32] transition-colors"
                    >
                      {sub.title}
                    </Link>
                  </DropdownMenuItem>
                ))}
              </DropdownMenuContent>
            </DropdownMenu>
          ) : (
            <Link
              key={menu.title}
              href={menu.href}
              className="hover:text-black transition-colors"
            >
              {menu.title}
            </Link>
          )
        )}
      </nav>
    </header>
  );
}
